<template>
  <div>
    <!--面包屑导航-->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>历史行程管理</el-breadcrumb-item>
      <el-breadcrumb-item :to="{ path: '/hodometer' }">历史出行数据管理</el-breadcrumb-item>
    </el-breadcrumb>
    <el-card>
      <!--搜索头部input框-->
      <el-row :gutter="15">
        <el-col :span="8">
          <el-input v-model="queryInfo.username" class="input-with-select" clearable placeholder="请输入内容"
                    @clear="getHodoMeterList">
            <el-button slot="append" icon="el-icon-search" @click="getHodoMeterList"></el-button>
          </el-input>
        </el-col>
        <el-col :span="5">
          <el-button type="primary" @click="addVisible=true">添加数据</el-button>
          <el-button type="danger" @click="batchDelete(tableChecked)">批量删除</el-button>
        </el-col>
      </el-row>
      <el-table
          :cell-style="rowClass"
          :row-style="rowStyle"
          :data="HodoMeterList"
          :header-cell-style="headClass"
          @selection-change="handleSelectionChange"
          border
          style="width: 100%"
      >
        <el-table-column type="selection" width="55"></el-table-column>
        <el-table-column
            label="ID"
            prop="id"
            width="45px">
        </el-table-column>
        <el-table-column
            label="姓名"
            prop="username"
            width="80px">
        </el-table-column>
        <el-table-column
            label="电话"
            prop="mobile"
            width="110">
        </el-table-column>
        <el-table-column
            label="外出地点"
            prop="place"
            width="100px">
        </el-table-column>
        <el-table-column
            label="出行时间"
            prop="cxTime">
        </el-table-column>
        <el-table-column
            label="备注"
            prop="bz">
        </el-table-column>
        <el-table-column
            label="操作"
        >
          <template slot-scope="scope">
            <el-tooltip class="item" content="编辑" effect="dark" placement="top-start">
              <el-button circle icon="el-icon-edit" size="mini" type="primary" @click="editHodo(scope.row)"></el-button>
            </el-tooltip>
            <el-tooltip class="item" content="删除" effect="dark" placement="top-start">
              <el-button circle icon="el-icon-delete" size="mini" type="danger"
                         @click="deleteHodo(scope.row)"></el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>
      <!--分页功能-->
      <el-pagination
          :current-page="queryInfo.pageNum"
          :page-size="queryInfo.pageSize"
          :page-sizes="[5,8,10,12,15]"
          :total="total"
          layout="total, sizes, prev, pager, next, jumper"
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange">
      </el-pagination>
    </el-card>
    <!--增加Info对话框-->
    <el-dialog
        :visible.sync="addVisible"
        title="增加数据"
        width="35%"
        @close="closeAddDialog"
    >
      <!--这是内容区域-->
      <el-form ref="addHodoFormRef" :model="HodoMeter" :rules="mobileFormRules" class="demo-Info" label-width="100px">
        <el-form-item label="用户名" prop="username">
          <el-input v-model="HodoMeter.username"></el-input>
        </el-form-item>
        <el-form-item label="电话" prop="mobile">
          <el-input v-model="HodoMeter.mobile"></el-input>
        </el-form-item>
        <el-form-item label="外出地点" prop="place">
          <el-input v-model="HodoMeter.place"></el-input>
        </el-form-item>
        <el-form-item label="外出时间" prop="cxTime">
          <el-date-picker
              style="float: left"
              v-model="HodoMeter.cxTime"
              placeholder="选择日期时间"
              size="large"
              type="datetime"
              value-format="yyyy-MM-dd HH:mm:ss">
          </el-date-picker>
        </el-form-item>
        <el-form-item label="备注" prop="bz">
          <el-input v-model="HodoMeter.bz"></el-input>
        </el-form-item>
      </el-form>
    <el-button @click="addVisible = false">取 消</el-button>
    <el-button type="primary" @click="addHodo">确 定</el-button>
    </el-dialog>
    <!--修改对话框-->
    <el-dialog
        :visible.sync="editVisible"
        title="编辑数据"
        width="35%"
        @close="closeEditDialog"
    >
      <!--这是内容区域-->
      <el-form ref="editHodoFormRef" :model="HodoMeter" :rules="mobileFormRules" class="demo-Info" label-width="100px">
        <el-form-item label="用户名" prop="username">
          <el-input v-model="HodoMeter.username" disabled></el-input>
        </el-form-item>
        <el-form-item label="电话" prop="mobile">
          <el-input v-model="HodoMeter.mobile"></el-input>
        </el-form-item>
        <el-form-item label="外出地点" prop="place">
          <el-input v-model="HodoMeter.place"></el-input>
        </el-form-item>
        <el-form-item label="外出时间" prop="cxTime">
          <el-date-picker
              style="float: left"
              v-model="HodoMeter.cxTime"
              placeholder="选择日期时间"
              size="large"
              type="datetime"
              value-format="yyyy-MM-dd HH:mm:ss">
          </el-date-picker>
        </el-form-item>
        <el-form-item label="备注" prop="bz">
          <el-input v-model="HodoMeter.bz"></el-input>
        </el-form-item>
      </el-form>
    <el-button @click="editVisible = false">取 消</el-button>
    <el-button type="primary" @click="updateHodo">确 定</el-button>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: "HodoMeter",
  data() {
    //验证手机号码的规则
    const checkMobile = (rule, value, cb) => {
      const regMobile = /^1[345789]\d{9}$/
      if (regMobile.test(value)) {
        return cb()
      }
      //返回一个错误提示
      cb(new Error('请输入合法的手机号码'))
    };
    return {
      queryInfo: {
        username: '',
        pageNum: 1,
        pageSize: 5,
      },
      total: null,
      HodoMeter: {
        place: '',
        cxTime: ''
      },
      tableChecked: [],//被选中的记录数据-----对应“批量删除”传的参数值
      ids: [],//批量删除id
      HodoMeterList: [],
      editVisible: false,
      addVisible: false,
      mobileFormRules: {
        mobile: [
          {validator: checkMobile, trigger: 'blur'}
        ],
      },
    }
  },
  methods: {
    handleSelectionChange(val) {
      this.tableChecked = val;
    },
    headClass() {  //表头居中显示
      return "text-align:center"
    },
    rowClass() { //表格数据居中显示
      return "text-align:center"
    },
    rowStyle({row}) {
      if (row.id % 2 == 0) {
        return {
          backgroundColor: '#eedeb0',
        }
      }
    },
    getHodoMeterList() {
      this.$axios.get('/admin/odometer/getOdoMeterList', {params: this.queryInfo}).then(res => {
        // console.log(res)
        if (res.data.data.total !== undefined) {
          if (res.data.status === 10000) {
            this.total = res.data.data.total
            this.HodoMeterList = res.data.data.list;
            this.$message.success("查询成功！")
          } else {
            this.$message.error("查询失败！")
          }
        } else {
          if (res.data.status === 10000) {
            this.total = 1;
            this.HodoMeterList = [];
            // console.log(this.HodoMeterList)
            this.HodoMeterList.push(res.data.data)
            this.$message.success("查询成功！")
          } else {
            this.$message.error("查询失败！")
          }
        }
      })
    },
    batchDelete(rows) {
      rows.forEach((item, index) => {
        this.ids.push(item.id);
        console.log(this.ids)
      });
      this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning',
        center: true
      }).then(() => {
        this.$axios.post('/admin/odometer/deleteUsers', this.ids).then(res => {
          if (res.data.status !== 10000) {
            this.$message.error("删除失败！");
          }
          this.$message({type: 'success', message: '删除成功!'});
          this.getHodoMeterList();
        });
        console.log('删除了', this.ids);
        this.ids = [];
      }).catch((e) => {
        this.$message({
          type: 'info',
          message: '已取消删除'
        });
      });
    },
    handleSizeChange(newSize) {
      this.queryInfo.pageSize = newSize;
      this.getHodoMeterList();
    },
    handleCurrentChange(newNum) {
      this.queryInfo.pageNum = newNum;
      this.getHodoMeterList();
    },
    editHodo(row) {
      // console.log(row)
      this.HodoMeter = row;
      this.editVisible = !this.editVisible;
    },
    closeAddDialog() {

    },
    closeEditDialog() {
      //重置表单
      this.$refs["editHodoFormRef"].resetFields();
    },
    deleteHodo(row) {
      // console.log(row.id);
      this.$confirm('此操作将永久删除该用户, 是否继续?', '删除用户', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        // console.log(row.id)
        // 点击确定，发起删除
        this.$axios.get('admin/odometer/delete/' + row.id).then(res => {
          this.getHodoMeterList();
          this.$message.success("删除用户成功！")
        })
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消删除'
        });
      });
    },
    updateHodo() {
      this.$refs["editHodoFormRef"].validate((valid) => {
        if (!valid) {
          return alert("请输入正确的信息！")
        } else {
          this.$axios.post('/odometer/update', this.HodoMeter).then(res => {
            if (res.data.status === 10000) {
              this.$message.success("更新成功！")
              this.editVisible = !this.editVisible;
              this.getHodoMeterList();
            } else {
              this.$message.error("更新失败！")
            }
          })
        }
      })
    },
    addHodo() {
      this.$refs["addHodoFormRef"].validate((valid) => {
        if (!valid) {
          return alert("请输入正确的信息！")
        } else {
          this.$axios.post('/odometer/add', this.HodoMeter).then(res => {
            if (res.data.status === 10000) {
              this.$message.success("添加成功！")
              this.addVisible = !this.addVisible;
              this.getHodoMeterList();
            } else {
              this.$message.error("添加失败！")
            }
          })
        }
      })
    }
  },
  created() {
    this.getHodoMeterList();
  }
}
</script>

<style scoped>

</style>
